.button.primary {
  cursor:pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 12px 24px;
  border-radius: 100px;
  border: 0px solid #CA4238;
  background-color: transparent;
  text-transform: uppercase;
  font-size: 20px;
  font-family: "SF Compact Display Medium";
  line-height: 23px;
  box-shadow: 0px 4px 28px rgba(149, 157, 165, 0.5);
  margin: 0px 12px;
  background: #CA4238;
  color: #FAFAFA;
}
 
.button.primary:hover {
  filter:saturate(70%)
}
 
.button.secondary:hover {
  text-decoration: underline;
}
 
.button.secondary {
  cursor:pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 24px;
  font-family: "SF Compact Display Medium";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: #CA4238;
  background: none;
  border:none;
  margin: 0px 8px;
}
 
.button.nav {
  cursor:pointer;
  position: static;
  padding: 8px 16px;
  font-family: "SF Compact Display Medium";
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
  background: none;
  border-radius: 100px;
  border: none;
  color: #050505;
}
 
.button.nav.selected {
  background: #FAECEB;
  color: #CA4238;
}

.button.nav:hover {
  background: #E1E4E8;
}